import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { RelatoriosComponent } from './relatorios.component';
import { RelatorioAguaComponent } from './relatorio-agua/relatorio-agua.component';
import { RelatorioPesoComponent } from './relatorio-peso/relatorio-peso.component';
import { RelatorioSonoComponent } from './relatorio-sono/relatorio-sono.component';

const routes: Routes = [
  {
    path: '',
    component: RelatoriosComponent,
    children: [
      {
        path: 'agua',
        component: RelatorioAguaComponent
      },
      {
        path: 'peso',
        component: RelatorioPesoComponent
      },
      {
        path: 'sono',
        component: RelatorioSonoComponent
      },
      {
        path: '**',
        redirectTo: 'agua'
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class RelatoriosRoutingModule { }
